import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import {
  Card,
  Row,
  Col,
  Spin,
  message,
  Divider
} from 'antd';

import { getQueryParams } from 'utils/urlUtil';
import { makeid } from 'utils/functionUtil';
import { getListDataValid } from 'utils/formUtil';
import { getStudentToGraduate } from 'services/StudentToGraduate';
import {
  createMultipleDiploma,
  exportPdfDiploma,
  updateDiplomaInfo,
} from 'services/Diploma';

import ModalExportNote from './modalExportNote';
import ModalImportNote from './modalImportNote';
import StudentCheckGraduateTable from './StudentCheckGraduateTable';
import FormSearch from './FormSearch';
import './style.scss';

class ListStudentPreparingToGraduationPage extends Component {
  constructor(props) {
    super(props);
    this.refFormForProject = React.createRef();
    this.refFormRawForProject = React.createRef();
    this.state = {
      loading: false,
      paramSearch: {},
      reloadForDataIssueCer: 0,
      selectedRows: [],
      rowKey: "autoKey",
      dataStudentForIssueCer: [],
      dataStudentForIssueCerRaw: [],
      showExportNoteModal: false,
      showImportNoteModal: false,
      loadingExportNoteModal: false,
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    const paramSearch = getQueryParams();
    this.setState({
      paramSearch,
    },
      () => this.handleSearch(paramSearch),
    );
  };

  handleSearch = async (
    paramSearch
  ) => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    if (
      !!paramSearch.trainTypeId &&
      !!paramSearch.facultyId &&
      !!paramSearch.courseId &&
      !!paramSearch.studyFieldId &&
      !!paramSearch.yearEduId &&
      !!paramSearch.sessionNo
    ) {
      this.setState({
        loading: true,
      });
      await Promise.all([
        getStudentToGraduate(paramSearch)
      ])
        .then((responses) => {
          const tempDataStudentForIssueCer = responses[0].data.items.map(item => {
            item.autoKey = `${makeid(8)}_${item.studentCode}`;
            item.canBeUpdate = item.signNoInTrack === null && item.returnDate === null;
            return item;
          })
          this.setState({
            loading: false,
            dataStudentForIssueCer: [...tempDataStudentForIssueCer],
            dataStudentForIssueCerRaw: [...tempDataStudentForIssueCer],
          });
        })
        .catch((error) => {
          console.log(error);
          this.setState({
            loading: false,
          });
        });
    }
  }

  handleSelectedRows = (selectedRows) => {
    this.setState({
      selectedRows,
    });
  }

  createMultipleSignNo = () => {
    try {
      const { dataStudentForIssueCer } = this.state;
      const listFieldNameNeedCheck = [
        'signNoFrom',
        'signNoTo',
        'signNoHead',
        'signNoTail',
      ];
      this.refFormForProject.current.validateFields(listFieldNameNeedCheck, async (errorRequiredField, requiredFieldValues) => {
        if (errorRequiredField) {
          return;
        }

        const listDiplomaDontHaveSignNo = dataStudentForIssueCer.filter(item => item.signNo === null);
        if (listDiplomaDontHaveSignNo.length === 0) {
          message.warn('Khng c sinh vin cn lp s bng.');
          return;
        }

        // check signNoFrom < signNoTo
        if (!(requiredFieldValues.signNoFrom <= requiredFieldValues.signNoTo)) {
          message.warn('S bng t ln hn s bng n.');
          return;
        }

        // check signNo length equal length of selected rows
        const lengthOfSignNo = requiredFieldValues.signNoTo - requiredFieldValues.signNoFrom + 1;
        if (lengthOfSignNo < listDiplomaDontHaveSignNo.length) {
          message.warn('S bng nh hn s lng bng ang c chn.');
          return;
        }

        this.setState({
          loading: true,
        });

        // create diplomaId
        let signNoFromLocal = parseInt(requiredFieldValues.signNoFrom);
        const signNoHeadLocal = requiredFieldValues.signNoHead;
        const signNoTailLocal = requiredFieldValues.signNoTail;
        const listSelectedItemWithSignNo = dataStudentForIssueCer.map(diplomaInfo => {
          const newDiploma = listDiplomaDontHaveSignNo.find(newDiplomaItem => newDiplomaItem.id === diplomaInfo.id);
          if (newDiploma) {
            diplomaInfo.signNo = `${signNoHeadLocal}${signNoFromLocal}${signNoTailLocal}`;
            signNoFromLocal += 1;
          }
          return diplomaInfo;
        })

        this.setState({
          loading: false,
          dataStudentForIssueCer: [...listSelectedItemWithSignNo],
        });

      });
    } catch (error) {
      console.log(error);
    }
  }

  saveMultipleDiploma = () => {
    try {
      const { selectedRows, rowKey, paramSearch } = this.state;
      if (selectedRows && selectedRows.length) {
        const listFieldNameNeedCheck = [
          'approvalNo',
          'approvalDate',
          'signedBy',
        ];
        this.refFormForProject.current.validateFields(listFieldNameNeedCheck, async (errorRequiredField, requiredFieldValues) => {
          if (errorRequiredField) {
            return;
          }
          const listFieldName = [];
          getListDataValid(rowKey, listFieldName, this.refFormForProject, this.refFormRawForProject, selectedRows)
            .then(
              async (listSelectedItem) => {
                // check signNo exist or signNoInTrack is null or returnDate is null
                const listDiplomaNoSignNo = listSelectedItem.every(diplomaInfo => diplomaInfo.signNo === null);
                if (listDiplomaNoSignNo) {
                  message.warn('C sinh vin cha c s hiu bng.');
                  return;
                }

                this.setState({
                  loading: true,
                });

                const formData = {
                  signedBy: requiredFieldValues.signedBy,
                  approvalNo: requiredFieldValues.approvalNo,
                  approvalDate: requiredFieldValues.approvalDate,
                  students: listSelectedItem.map(diplomaInfo => ({
                    studentId: diplomaInfo.studentId,
                    signNo: diplomaInfo.signNo,
                    signNoInTrack: diplomaInfo.signNoInTrack,
                    returnDate: diplomaInfo.returnDate,
                    fileId: diplomaInfo.fileId,
                  }))
                };;

                try {
                  await createMultipleDiploma(formData);
                  message.success('Xt cp vn bng thnh cng thnh cng.');
                  this.setState({
                    selectedRows: [],
                    loading: false,
                  },
                    () => this.handleSearch(paramSearch),
                  );
                } catch (error) {
                  console.log(error);
                } finally {
                  this.setState({
                    loading: false,
                  });
                }
              }
            )
            .catch(
              () => {
                this.setState({
                  loading: false,
                });
              }
            )
        });
      }
    } catch (error) {
      console.log(error);
    }
  }

  updateDiploma = async (record, signNoInTrack, returnDate) => {
    try {
      const { paramSearch } = this.state;
      this.setState({
        loading: true,
      });
      const payload = {
        studentIds: [record.id],
        signNoInTrack,
        returnDate: returnDate.toDate(),
      };
      await updateDiplomaInfo(payload);
      this.setState({
        loading: false,
      },
        () => this.handleSearch(paramSearch),
      );
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }

  handleExportPdfDiploma = async (studentId) => {
    try {
      this.setState({
        loading: true,
      });
      await exportPdfDiploma(studentId);
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  }
  
  handleImportPdfDiploma = () => {
    this.showImportNoteModal();
  }

  showExportNoteModal = async () => {
    this.setState({
      showExportNoteModal: true,
    });
  }

  hideExportNoteModal = () => {
    this.setState({
      showExportNoteModal: false,
    });
  }
  
  showImportNoteModal = () => {
    this.setState({
      showImportNoteModal: true,
    });
  }

  hideImportNoteModal = () => {
    const { paramSearch } = this.state;
    this.handleSearch(paramSearch);
    this.setState({
      showImportNoteModal: false,
    });
  }

  handleFilterByStudentCodeOrName = (studentCodeOrName) => {
    const { dataStudentForIssueCerRaw } = this.state;
    let tempFilterData = [];
    const studentCodeOrNameTrim = studentCodeOrName.trim();
    if (studentCodeOrNameTrim !== "") {
      tempFilterData = dataStudentForIssueCerRaw.filter(item =>
        (`${item.studentCode}`.search(studentCodeOrNameTrim) !== -1)
        ||
        (`${item.fullName}`.search(studentCodeOrNameTrim) !== -1)
      )
    } else {
      tempFilterData = [...dataStudentForIssueCerRaw];
    }

    this.setState({
      dataStudentForIssueCer: tempFilterData,
    });
  }

  handleUploadImageForStudent= (studentId, fileId) => { 
    const { dataStudentForIssueCer } = this.state;
    dataStudentForIssueCer.forEach(item => {
      if(item.studentId === studentId) {
        item.fileId = fileId;
      }
    })
    this.setState({
      dataStudentForIssueCer,
    });
  }

  render() {
    const {
      dataStudentForIssueCer,
      dataStudentForIssueCerRaw,
      reloadForDataIssueCer,
      rowKey,
      loading,
      selectedRows,
      showExportNoteModal,
      loadingExportNoteModal,
      showImportNoteModal
    } = this.state;
    const disableNoSelectedRowBtn = !(selectedRows && selectedRows.length);
    const haveData = (dataStudentForIssueCer && dataStudentForIssueCer.length)
                    ||
                     (dataStudentForIssueCerRaw && dataStudentForIssueCerRaw.length);
    return (
      <Spin spinning={loading}>
        <Card title="Xt iu kin kha lun, thi tt nghip">
          <Row>
            <FormSearch />
          </Row>
          {haveData ?
            <Row gutter={24}>
              <Divider />
              <Col span={24}>
                <StudentCheckGraduateTable
                  reload={reloadForDataIssueCer}
                  data={dataStudentForIssueCer}
                  ref={this.refFormForProject}
                  handleSelectedRows={this.handleSelectedRows}
                  rowKey={rowKey}
                  wrappedComponentRef={(form) => {
                    this.refFormRawForProject = form;
                  }}
                  createMultipleSignNo={this.createMultipleSignNo}
                  saveMultipleDiploma={this.saveMultipleDiploma}
                  exportPDF={this.handleExportPdfDiploma}
                  exportPDFNote={this.showExportNoteModal}
                  updateDiploma={this.updateDiploma}
                  disableNoSelectedRowBtn={disableNoSelectedRowBtn}
                  // disableNoParamsBtn={disableNoParamsBtn}
                  handleFilterByStudentCodeOrName={this.handleFilterByStudentCodeOrName}
                  importPDF={this.handleImportPdfDiploma}
                  uploadImageForStudent={this.handleUploadImageForStudent}
                />
              </Col>
            </Row>
            :
            null
          }
        </Card>
        <ModalExportNote
          visible={showExportNoteModal}
          loading={loadingExportNoteModal}
          onSuccess={this.hideExportNoteModal}
          onCancel={this.hideExportNoteModal}
        />
        <ModalImportNote
          visible={showImportNoteModal}
          onSuccess={this.hideImportNoteModal}
          onCancel={this.hideImportNoteModal}
        />
      </Spin>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListStudentPreparingToGraduationPage />
  </Page>
);



