import React, { Component, Fragment } from 'react';
import { Row, Form, Col, Input, DatePicker } from 'antd';
import { Btn, BtnExport, BtnSave, BtnSearch } from 'components/common/button';
import { TableForm, InputOnlyNumber } from 'components/common';
import moment from 'moment';
import { requiredRule } from 'utils/validateRules';
import AvatarUpload from './AvatarUpload';

@Form.create({ name: 'formTableQualifiedDoThesis' })
class TableQualifiedDoThesis extends Component {
  constructor(props) {
    super(props);
    this.refTable = React.createRef();
    this.dateFormatList = ['DD/MM/YYYY', 'DD/MM/YY'];
    this.state = {};
  }

  getTableData = () => {
    return this.refTable.current.getTableData();
  }

  createColumns = (updateDiploma, exportPDF) => {
    const columns1 = [
      {
        title: 'M sinh vin',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'H v tn',
        dataIndex: 'fullName',
        key: 'fullName',
      },
      {
        title: 'Tn lp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'TBCTN',
        dataIndex: 'totalGrade',
        key: 'totalGrade',
      },
      {
        title: 'Xp loi TN',
        dataIndex: 'gradeClassificationLabel',
        key: 'gradeClassificationLabel',
      },
      {
        title: 'S hiu bng',
        dataIndex: 'signNo',
        key: 'signNo',
      },
      {
        title: 'S Q',
        dataIndex: 'approvalNo',
        key: 'approvalNo',
      },
      {
        title: 'Ngy Q',
        dataIndex: 'approvalDate',
        key: 'approvalDate',
        render: (text, record) => <span>{record.approvalDate && moment(record.approvalDate).format('DD-MM-YYYY')}</span>,
      },
      {
        title: 'S vo s',
        dataIndex: 'signNoInTrack',
        key: 'signNoInTrack',
        render: (text, record) => {
          const { getFieldDecorator } = this.props.form;
          const { signNoInTrack } = record;
          return signNoInTrack ?
            <span>{record.signNoInTrack}</span>
            :
            <Form.Item >
              {getFieldDecorator(`signNoInTrack-${record.autoKey}`, {
                rules: [
                  {
                    required: true,
                    message: 'Vui lng in s vo s!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
        },
      },
      {
        title: 'Ngy cp',
        dataIndex: 'returnDate',
        key: 'returnDate',
        render: (text, record) => {
          const { getFieldDecorator } = this.props.form;
          const { returnDate } = record;
          return returnDate ?
            <span>{moment(record.returnDate).format('DD-MM-YYYY')}</span>
            :
            <Form.Item>
              {getFieldDecorator(`returnDate-${record.autoKey}`, {
                rules: [
                  {
                    required: true,
                    message: 'Vui lng in ngy nhn bng!',
                  },
                ],
              })(
                <DatePicker
                  format={this.dateFormatList}
                />
              )}
            </Form.Item>
        },
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => {
          return (
            <Fragment>
              <AvatarUpload
                profileId={record.id}
                imgStorageId={record.fileId}
                studentId={record.studentId}
                studentCode={record.studentCode}
                uploadImageForStudent={this.props.uploadImageForStudent}
              />
              <Btn
                title="Xem"
                onClick={() => exportPDF(record.studentId)}
              />
            </Fragment>
          )
        },
      },
    ];
    return columns1;
  };

  handleFilterByStudentCodeOrName = () => {
    const { handleFilterByStudentCodeOrName } = this.props;
    const { getFieldValue } = this.props.form;
    const studentCodeOrName = getFieldValue('studentCodeOrName');
    handleFilterByStudentCodeOrName(studentCodeOrName);
  }

  render() {
    const {
      reload,
      rowKey,
      data,
      handleSelectedRows,
      form,
      createMultipleSignNo,
      saveMultipleDiploma,
      exportPDF,
      exportPDFNote,
      disableNoSelectedRowBtn,
      updateDiploma,
      importPDF,
    } = this.props;
    const columns = this.createColumns(updateDiploma, exportPDF);
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 18 },
      },
    };
    return (
      <Fragment>
        <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
          <Row gutter={24}>
            <Col xs={24} sm={12} md={8}>
              <Form.Item label="S Q TN">
                {getFieldDecorator('approvalNo', {
                  rules: [requiredRule],
                })(
                  <Input />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={8}>
              <Form.Item {...formItemLayout} label="Ngy Q TN">
                {getFieldDecorator('approvalDate', {
                  rules: [requiredRule],
                })(
                  <DatePicker
                    defaultValue={moment(new Date(), this.dateFormatList[0])}
                    format={this.dateFormatList}
                    style={{ width: '100%' }}
                  />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={8}>
              <Form.Item {...formItemLayout} label="Ngi k">
                {getFieldDecorator('signedBy', {
                  rules: [requiredRule],
                })(
                  <Input />
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="u s bng">
                {getFieldDecorator('signNoHead', {
                  rules: [requiredRule],
                })(
                  <Input />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="S bng t">
                {getFieldDecorator('signNoFrom', {
                  rules: [requiredRule],
                })(
                  <InputOnlyNumber placeholder="T s" />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="S bng n">
                {getFieldDecorator('signNoTo', {
                  rules: [requiredRule],
                })(
                  <InputOnlyNumber placeholder="n s" />
                )}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="ui s bng">
                {getFieldDecorator('signNoTail', {
                  rules: [requiredRule],
                })(
                  <Input />
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col xs={24} sm={12} md={8}>
              <Col span={15}>
                <Form.Item label="B lc">
                  {getFieldDecorator('studentCodeOrName', {
                    rules: [requiredRule],
                  })(
                    <Input placeholder="M hoc Tn sinh vin" />
                  )}
                </Form.Item>
              </Col>
              <Col span={9}>
                <div className="btn-group">
                  <BtnSearch onClick={this.handleFilterByStudentCodeOrName} />
                </div>
              </Col>
            </Col>
            <Col xs={24} sm={12} md={16}>
              <div className="btn-group" style={{ float: 'right' }}>
                <BtnExport title="Nhp thng tin vn bng" onClick={importPDF} />
                <Btn icon="book" className="btnSave" title="Lp s hiu bng" onClick={createMultipleSignNo} />
                <BtnSave title="Lu thng tin vn bng" onClick={saveMultipleDiploma} disabled={disableNoSelectedRowBtn} />
                <BtnExport title="Xut ghi ch vn bng" onClick={exportPDFNote} />
              </div>
            </Col>
          </Row>
          <TableForm
            reload={reload}
            data={data || []}
            handleSelectedRows={handleSelectedRows}
            rowKey={rowKey}
            columns={columns}
            form={form}
            ref={this.refTable}
          />
        </Form>
      </Fragment>
    );
  }
}

export default TableQualifiedDoThesis;



