import React from 'react';
import moment from 'moment';
import { Upload, Icon, message, Spin, DatePicker, Row, Col, Form, Input } from 'antd';
import { SelectAjax } from 'components/common';
import { UploadDiploma } from 'services/Diploma';
import { getYearEducations } from 'services/yearEducationService';
import { getStudentToGraduateSession } from 'services/StudentToGraduate';
import './style-avatar.scss';

function beforeUpload(file) {
  const isExcel = file.type === '.csv' || file.type === 'application/vnd.ms-excel' || file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet';
  if (!isExcel) {
    message.error('Ch c th upload file Excel!');
  }
  const isLt2M = file.size / 1024 / 1024 < 2;
  if (!isLt2M) {
    message.error('File khng c qu 2MB!');
  }
  return isExcel && isLt2M;
}

@Form.create({ name: 'ModalImportDiploma' })
class ImportDiploma extends React.Component {
  state = {
    loading: false,
  };

  handleUpload = async file => {
    this.setState({ loading: true });
    const listFieldNameNeedCheck = [
      'approvalNo',
      'approvalDate',
      'signedBy',
      'yearEduId',
      'sessionNo',
    ];
    this.props.form.validateFields(listFieldNameNeedCheck, async (errorRequiredField, requiredFieldValues) => {
      if (errorRequiredField) {
        this.setState({ loading: false });
        return;
      }
      try {
        console.log(file);
        const formData = new FormData();
        formData.append('file', file);
        const queryParams = {
          signedBy: requiredFieldValues.signedBy,
          approvalNo: requiredFieldValues.approvalNo,
          approvalDate: moment(requiredFieldValues.approvalDate).format("YYYY-MM-DD"),
          yearEduId: requiredFieldValues.yearEduId,
          sessionNo: requiredFieldValues.sessionNo,
        }
        await UploadDiploma(queryParams, formData);
        message.success("Nhp d liu thnh cng.");
        this.props.onSuccess();
      } catch (error) {
        message.error('C li xy ra!');
      } finally {
        this.setState({ loading: false });
      }
    })
  };

  render() {
    const {
      getFieldDecorator,
      getFieldValue,
      setFieldsValue
    } = this.props.form;
    const dateFormatList = ['DD/MM/YYYY', 'DD/MM/YY'];
    const yearEduId = getFieldValue('yearEduId');
    const layout = {
      labelCol: { span: 8 },
      wrapperCol: { span: 16 },
    };
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 18 },
      },
    };
    const uploadButton = (
      <div>
        <Icon type={this.state.loading ? 'loading' : 'upload'} />
        <div className="ant-upload-text">Upload</div>
      </div>
    );
    return (
      <Spin spinning={this.state.loading} className="staff-avatar">
        <Form {...layout} onSubmit={this.handleSave}>
          <Row gutter={24}>
            <Col span={20} offset={2}>
              <Form.Item {...formItemLayout} label="S Q TN">
                {getFieldDecorator('approvalNo', {
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp s Q!',
                    },
                  ],
                })(
                  <Input style={{ width: '100%' }} />
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={20} offset={2}>
              <Form.Item {...formItemLayout} label="Ngy Q TN">
                {getFieldDecorator('approvalDate', {
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp Ngy Q TN!',
                    },
                  ],
                })(
                  <DatePicker
                    defaultValue={moment(new Date(), dateFormatList[0])}
                    format={dateFormatList}
                    style={{ width: '100%' }}
                  />
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={20} offset={2}>
              <Form.Item {...formItemLayout} label="Ngi k">
                {getFieldDecorator('signedBy', {
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng nhp Ngi k',
                    },
                  ],
                })(
                  <Input style={{ width: '100%' }} />
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={20} offset={2}>
              <Form.Item {...formItemLayout} label="Nm hc">
                {getFieldDecorator('yearEduId', {
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng chn Nm hc',
                    },
                  ],
                })(
                  <SelectAjax
                    service={getYearEducations}
                    setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                    typeValue="int"
                  />
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={20} offset={2}>
              <Form.Item {...formItemLayout} label="t">
                {getFieldDecorator('sessionNo', {
                  rules: [
                    {
                      required: true,
                      message: 'Vui lng chn t',
                    },
                  ],
                })(
                  <SelectAjax
                    setFieldsValue={setFieldsValue}
                    service={getStudentToGraduateSession}
                    params={{ yearEduId }}
                    allowGetData={!!yearEduId}
                    typeValue="int"
                  />
                )}
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={20} offset={2}>
              <div style={{ float: 'right' }}>
                <Upload
                  name="avatar"
                  listType="picture-card"
                  className="avatar-uploader"
                  showUploadList={false}
                  action={this.handleUpload}
                  beforeUpload={beforeUpload}
                >
                  {(uploadButton)}
                </Upload>
              </div>

            </Col>
          </Row>
        </Form>
      </Spin>
    );
  }
}

export default ImportDiploma;



