import React, { Component } from 'react';
import { Form, Row, Col } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { BtnSearch, BtnClear } from 'components/common/button';
import { requiredRule } from 'utils/validateRules';
import { SelectAjax } from 'components/common';
import { getYearEducations } from 'services/yearEducationService';
import {
  getStudentToGraduateSession,
} from 'services/StudentToGraduate';

import './style.scss';

@Form.create({ name: 'formTeacher' })
class FormFilterReward extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    updateQueryParams();
  };

  render() {
    const {
      getFieldDecorator,
      getFieldValue,
      setFieldsValue
    } = this.props.form;
    const yearEduId = getFieldValue('yearEduId');
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn H!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Kha',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn CN',
          },
        ],
      },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
        </Row>
        <Row gutter={24}>
          <Col span={5}>
            <Form.Item label="Nm hc">
              {getFieldDecorator('yearEduId', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="t">
              {getFieldDecorator('sessionNo', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getStudentToGraduateSession}
                  params={{ yearEduId }}
                  allowGetData={!!yearEduId}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col>
            <div className="btn-group" style={{ float: 'right' }}>
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormFilterReward;



