import React from 'react';
import { Button, Upload, Icon, message, Spin } from 'antd';
import { hostAPI } from 'utils';
import { uploadFile } from 'services/fileStorage';
import './style-avatar.scss';

function beforeUpload(file) {
  const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
  if (!isJpgOrPng) {
    message.error('Ch c th upload file JPG hay PNG!');
  }
  const isLt2M = file.size / 1024 / 1024 < 2;
  if (!isLt2M) {
    message.error('File khng c qu 2MB!');
  }
  return isJpgOrPng && isLt2M;
}

class ImageUpload extends React.Component {
  state = {
    loading: false,
    imgStorageId: "",
    fileList: [],
    isUpdateFile: false,
  };

  componentWillMount() {
    if (this.props.imgStorageId !== null && this.props.imgStorageId !== undefined) {
      const { fileList } = this.state;
      const fileInfo = {
        uid: '-1',
        name: this.props.studentCode,
        status: 'done',
        url: `${hostAPI}/api/filestorage/Storage/${this.props.imgStorageId}/Download`
      }
      fileList.push(fileInfo);
      this.setState({
        fileList,
        imgStorageId: this.props.imgStorageId
      });
    }
  }

  componentWillReceiveProps(nextProps) {
    const { fileList, isUpdateFile } = this.state;
    if (
      nextProps.imgStorageId !== this.state.imgStorageId &&
      (nextProps.imgStorageId !== null && nextProps.imgStorageId !== undefined) &&
      isUpdateFile === false
    ) {
      const fileInfo = {
        uid: '-1',
        name: 'image',
        status: 'done',
        url: `${hostAPI}/api/filestorage/Storage/${nextProps.imgStorageId}/Download`
      }
      fileList.push(fileInfo);
      this.setState({
        fileList,
        imgStorageId: nextProps.imgStorageId
      });
    }
  }
  handleUpload = async file => {
    this.setState({ loading: true });
    try {
      const formData = new FormData();
      formData.append('file', file);
      const result = await uploadFile(formData);
      if (result.status === 200 && result.data) {
        const fileInfo = {
          uid: '-1',
          name: result.data.fileName,
          status: 'done',
          url: `${hostAPI}/api/filestorage/Storage/${result.data.id}/Download`
        }
        this.setState({
          fileList: [fileInfo],
          imgStorageId: result.data.id,
          isUpdateFile: true,
        });
        this.props.uploadImageForStudent(this.props.studentId, result.data.id)
      } else {
        message.error('C li xy ra!');
      }
    } catch (error) {
      message.error('C li xy ra!');
    }
    this.setState({ loading: false });
  };

  render() {
    const { fileList } = this.state;
    return (
      <Spin spinning={this.state.loading} className="staff-avatar">
        <Upload
          name="importDiploma"
          className="avatar-uploader"
          action={this.handleUpload}
          beforeUpload={beforeUpload}
          fileList={fileList}
        >

          {fileList.length ?
            <Button className="btn-select">
              <Icon type={this.state.loading ? 'loading' : 'upload'} />
              <span>Chnh sa</span>
            </Button>
            :
            <Button className="btn-select">
              <Icon type={this.state.loading ? 'loading' : 'upload'} />
              <span>Ti ln</span>
            </Button>
          }
        </Upload>
      </Spin>
    );
  }
}

export default ImageUpload;



