import React, { Component } from 'react';
import {
  Row,
  Col,
  Form,
  Modal,
  Spin,
  Input
} from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { exportPdfDiplomaNote } from 'services/Diploma';

@Form.create({ name: 'ModalCopyProgram' })
class ModalCopyProgram extends Component {
  state = {
    loading: true,
  };

  componentWillReceiveProps(nextProps) {
    if (nextProps.loading !== this.state.loading) {
      this.setState({
        loading: nextProps.loading,
      });
    }
  }

  handleSave = () => {
    this.props.form.validateFields(async (err, values) => {
      try {
        this.setState({
          loading: true,
        });
        await exportPdfDiplomaNote({
          approveNo: values.approveNo,
          trainTypeId: values.trainTypeId,
          facultyId: values.facultyId,
          courseId: values.courseId,
        });
        this.props.onSuccess();
      } catch (error) {
        this.setState({
          loading: false,
        });
      }
    });
  }

  render() {
    const { loading } = this.state;
    const { getFieldDecorator } = this.props.form;
    const layout = {
      labelCol: { span: 8 },
      wrapperCol: { span: 16 },
    };
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 18 },
      },
    };
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn H!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Kha',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn CN',
          },
        ],
      },
    };
    return (
      <Modal
        title='Xut ghi ch vn bng'
        visible={this.props.visible}
        onOk={this.handleSave}
        okText="Xut"
        onCancel={this.props.onCancel}
        cancelText="Hy"
        width={550}
      >
        <Spin spinning={loading}>
          <Form {...layout} onSubmit={this.handleSave}>
            <Row gutter={24}>
              <Col span={20} offset={2}>
                <Form.Item {...formItemLayout} label="S Q">
                  {getFieldDecorator('approveNo', {
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp s Q!',
                      },
                    ],
                  })(
                    <Input style={{ width: '100%' }} />
                  )}
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={24}>
              <Col span={20} offset={2}>
                <FilterPathEducate
                  classes
                  column
                  form={this.props.form}
                  optionFieldDecorator={optionFieldDecorator}
                />
              </Col>
            </Row>
          </Form>
        </Spin>

      </Modal>
    );
  }
}

export default ModalCopyProgram;



