import { request, requestDownload } from 'utils';

const prefix = '/api/education';

export const createMultipleDiploma = (data) =>
  request({
    prefix,
    url: `/Diploma`,
    data,
    method: 'POST',
  });

export const updateDiplomaInfo = (data) =>
  request({
    prefix,
    url: `/Diploma/UpdateDiplomaInfo`,
    data,
    method: 'POST',
  });

export const exportPdfDiploma = (studentId) =>
  requestDownload({
    prefix,
    defaultFileName: `Diploma.excel`,
    url: `/Diploma/ExportPdfDiploma`,
    method: 'POST',
    data: {
      studentId
    },
  });

export const exportPdfDiplomaNote = ({
  approveNo,
  trainTypeId,
  facultyId,
  courseId,
}) =>
  requestDownload({
    prefix,
    defaultFileName: `DiplomaNote.pdf`,
    url: `/Diploma/ExportPdfDiplomaNote`,
    method: 'POST',
    data: {
      approveNo,
      trainTypeId,
      facultyId,
      courseId,
    },
  });

export const UploadDiploma = (params, data) =>
  request({
    prefix,
    url: `/Diploma/UploadDiploma`,
    method: 'POST',
    params,
    data,
  });

